import axios from 'axios';
import { OcpCloudReportTypePaths } from './ocpCloudReports';
export function runExport(reportType, query) {
    const path = OcpCloudReportTypePaths[reportType];
    return axios.get(`${path}?${query}`, {
        headers: {
            Accept: 'text/csv',
        },
    });
}
